<template>
  <div id="myModal" class="modal">

    <!-- Modal content -->
    <div class="modal-content">
      <span class="close" @click="$emit('close')" >&times;</span>
      <p>You must be logged in to ask a question.</p>
      <div class="modalButtonContainer">
      <button class="btn btn-info modalButton" @click="toLogin" >Log in</button>
      <button class="btn btn-info modalButton" @click="toSignup">Sign up</button>
      </div>
    </div>

  </div>
</template>
<script>
export default {
  name: 'LoginPromptModal',

  methods: {
    toLogin() {
      this.$router.push('/login');
    },
    toSignup() {
      this.$router.push('/signup');
    }
  }
}
</script>


<style  scoped>
.modal {
  display: block;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  max-width: 300px;
  /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  text-align: right;
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;

}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.modalButton {
  margin: 10px;
  color: #fff;
}
</style>